.menus {
    padding: 5rem 0;
    overflow-x: hidden;

    &__tagline {
        font-size: 1.7rem;
        padding-left: 1rem;
        color: $greyDark;
        margin-bottom: 1rem;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            height: 100%;
            width: 0.4rem;
            background-color: #191919;
        }
    }

    &__title {
        font-size: 2.5rem;
        font-weight: 500;
    }

